import { ImageVolume, utilities as csUtils } from '@cornerstonejs/core';
import type { Types } from '@cornerstonejs/core';

import { getBoundingBoxAroundShape } from '../../../utilities/boundingBox';
import { pointInShapeCallback } from '../../../utilities';
import { triggerSegmentationDataModified } from '../../../stateManagement/segmentation/triggerSegmentationEvents';

const { transformWorldToIndex } = csUtils;

type OperationData = {
    segmentationId: string;
    volume: ImageVolume;
    oldScalarData: ImageVolume;
};

/**
 * Simply replace the current segmentation data by the oldScalarData put in parameter
 * @param enabledElement
 * @param operationData
 */
export function fillReplace(
    enabledElement: Types.IEnabledElement,
    operationData: OperationData,
): void {
    const {
        volume: segmentation,
        segmentationId,
        oldScalarData,
    } = operationData;

    const scalarData = segmentation.getScalarData();

    for(let i = 0; i < scalarData.length; i++){
        scalarData[i] = oldScalarData[i]
    }

    triggerSegmentationDataModified(segmentationId);

}
